.country-usage-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row
}

.country-usage-graph {
    width: 70%;
}

.country-usage-list {
    width: 25%;
    max-height: 300px;
    overflow-y: auto;
    padding: 16px;
}

@media only screen and (max-width: 800px) {
    .country-usage-container {
        display: block;
    }
    .country-usage-list {
        width: 40%;
        margin: 0 auto;
    }

    .country-usage-graph {
        width: 100%;
    }
}

.unavailable-message {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}



